@import "../styles/variables.scss";

.header {
  display: flex;

  &__logo {
    width: calc(#{$header-hight} - 4px);
    font-size: 2.5rem;
    cursor: pointer;
  }

  &__link {
    color: inherit;
  }

  &__text {
    flex-grow: 1;
  }

  &__button {
    width: calc(#{$header-hight} - 25px);
    height: calc(#{$header-hight} - 25px);

    font-size: 1.2rem;
    margin: auto 0;

    @media (min-width: 320px) {
      width: calc(#{$header-hight} - 14px);
      height: calc(#{$header-hight} - 14px);
    }

    & .icon {
      transition: 0.4s transform;
    }

    &:hover .icon {
      transform: rotate(90deg);
    }

    &:active .icon {
      transform: rotate(180deg);
    }
  }
}
